	module.exports = {"data":{"markdownRemark":{"html":"<h1>tmux</h1>\n<p>Все хоткеи начинаются с <strong>Ctrl+b</strong>\nДалее описывается только вторая кнопка, которую нужно нажимать после <strong>b</strong>, то есть, <strong>с</strong> означает, что нужно нажать <strong>Сtrl+b c</strong></p>\n<p><strong>c</strong> - создать окошко</p>\n<p><strong>0..9</strong> - перейти в такое-то окошко</p>\n<p><strong>p</strong> - перейти в предыдущее окошко</p>\n<p><strong>n</strong> - перейти в следующее окошко</p>\n<p><strong>l</strong> - перейти в предыдущее активное окошко</p>\n<p><strong>&#x26;</strong> - закрыть окошко (можно набрать <strong>exit</strong>)</p>\n<h2>В одном окошке может быть много панелей:</h2>\n<p><strong>%</strong> - разделить текущую панель по вертикали</p>\n<p><strong>\"</strong> - разделить текущую панель по горизонтали</p>\n<p><strong>→←↑↓</strong> - переходы между панелями. Если не отпускать <strong>Ctrl</strong> при нажатии стрелочек, то будут изменяться размеры текущего окна</p>\n<p><strong>x</strong> - закрыть панель (можно набрать <strong>exit</strong>)</p>\n<h2>Скроллинг:</h2>\n<p><strong>PgUp</strong> - вход в режим \"копирования\", после чего:</p>\n<p>PgUp, PgDown - скроллинг</p>\n<p>q - выход из режима \"копирования\"</p>\n<p><a href=\"https://habrahabr.ru/post/327630/\">https://habrahabr.ru/post/327630/</a></p>","frontmatter":{"path":"/blog/tmux","title":"tmux"}}},"pathContext":{}}

